import React, {useEffect, useRef} from 'react';
import styled from "styled-components";

let beforeWidth = 20;

let Circle = styled.div`
    width: 100px;
    height: 100px;
    background: #fff;
    display: inline-block;
    border-radius: 50%;
    margin: 0 10px;
    animation: wavering 5s ease-in-out infinite;
    filter: blur(25px);
    cursor: pointer;
    position: relative;
    top: 0;
    left: 0;
    :before {
          content: '';
          position: absolute;
          top: -${beforeWidth}px;
          left: -${beforeWidth}px;
          right: -${beforeWidth}px;
          bottom: -${beforeWidth}px;
          background: #fff;
          border-radius: 50% ;
          z-index: -1;
          filter: blur(20px);          
    }
    ${props =>
    (props.i % 2 === 0 ? `
            :before{
                  background: linear-gradient(90deg,#f00,#0ff);
                  animation: rotate1 6s linear infinite;
            }
            background: linear-gradient(90deg,#f00,#0ff);
            `
            :
            `:before{
                background: linear-gradient(90deg,#ffeb3b,#da00ff);
                animation: rotate2 6s linear infinite;
            }
            background: linear-gradient(90deg,#ffeb3b,#da00ff);
            `
    )

}
`;


function App({ContainerRef, index}) {
    let Ref = useRef(null);
    useEffect(() => {
        if (Ref && ContainerRef) {
            let left = 0;
            let top = 0;

            Ref.current.onmousedown = function onmousedown(event) {
                document.body.style.cursor = 'grabbing';
                Ref.current.style.cursor = 'grabbing';

                let shiftX = event.clientX - left;
                let shiftY = event.clientY - top;
                moveAt(event.clientX, event.clientY);

                function moveAt(pageX, pageY) {
                    Ref.current.style.left = pageX - shiftX + 'px';
                    Ref.current.style.top = pageY - shiftY + 'px';
                    left = pageX - shiftX;
                    top = pageY - shiftY;
                }

                function onMouseMove(event) {
                    console.log(event.clientX, event.clientY);
                    moveAt(event.clientX, event.clientY);

                    Ref.current.style.visibility = 'hidden';
                    let elemBelow = document.elementFromPoint(event.clientX, event.clientY);
                    Ref.current.style.visibility = 'visible';

                    if (!elemBelow) {
                        document.removeEventListener('mousemove', onMouseMove);
                        return;
                    }

                    let droppableBelow = elemBelow.closest('.droppable');

                    if (!droppableBelow) {
                        document.removeEventListener('mousemove', onMouseMove);
                        document.body.style.cursor = '';
                        Ref.current.style.cursor = '';
                    }

                }

                document.addEventListener('mousemove', onMouseMove);
                document.addEventListener('mouseup', onMouseUp);

                function onMouseUp() {
                    document.removeEventListener('mousemove', onMouseMove);
                    document.body.style.cursor = '';
                    Ref.current.style.cursor = '';
                };

            };

            Ref.current.ondragstart = function () {
                return false;
            };
        }

    });

    return (
        <Circle ref={Ref} i={index}/>
    );
}

export default App;
