import React, {useEffect, useRef} from 'react';
import styled from "styled-components";

let Button = styled.button`
    position: relative;
    padding: 15px 40px;
    margin: 20px 0;
    color: #fff;
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 2px;
    border-radius: 40px;
    border: 0;
    overflow: hidden;
    cursor: pointer;
    background: ${props => props?.num === 1 ? 'linear-gradient(90deg,#0162c8,#55e7fc);' :
    'linear-gradient(90deg,#755bea,#ff72c0);'
} ;
    > span {
          position: absolute;
          background: #fff;
          transform: translate(-50%,-50%);
          pointer-events: none;
          border-radius: 50%;
          animation: wave 1s linear infinite;
    }
`;

function App({num}) {
    let buttonRef = useRef(null);

    function onClick(e) {
        if (buttonRef?.current) {
            let x = e.clientX - buttonRef.current.getBoundingClientRect().left;
            let y = e.clientY - buttonRef.current.getBoundingClientRect().top;

            let ripples = document.createElement("span");
            ripples.style.left = x + 'px';
            ripples.style.top = y + 'px';

            buttonRef.current.append(ripples);

            setTimeout(() => {
                ripples.remove()
            },1000)
        }
    }

    return (
        <Button ref={buttonRef} num={num} onClick={onClick}>
            button
        </Button>
    );
}

export default App;
