import React, {useEffect, useRef} from 'react';
import styled from "styled-components";

let Button = styled.button`
    position: relative;
    padding: 20px 50px;
    margin: 20vh 0;
    color: #03e9f4;
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 2px;
    border: 0;
    overflow: hidden;
    cursor: pointer;
    transition: 0.5s;
    background: rgba(97,218,251,0);
    -webkit-box-reflect: below 1px linear-gradient(
      transparent,#0005);
    :hover {       
        background: #03e9f4;
        color: #050801;
        box-shadow: 0 0 5px #03e9f4,
         0 0 25px #03e9f4,
         0 0 50px #03e9f4,
         0 0 150px #03e9f4;  
    }
    > span {
        position: absolute;
        display: block;
    }
    .neon-button_nth-child1 {
        top: 0;
        left: -100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(90deg, transparent, #03e9f4);
        transition: 0.5s;
        animation: animate1 1s linear infinite;
    }
    .neon-button_nth-child2 {
        top: -100%;
        right: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(180deg, transparent, #03e9f4);
        animation: animate2 1s linear infinite;
        animation-delay: 0.25s;
    }
    .neon-button_nth-child3 {
        bottom: 0;
        left: 100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(-90deg, transparent, #03e9f4);
        animation: animate3 1s linear infinite;
        animation-delay: 0.5s;
    }
    .neon-button_nth-child4 {
        top: 100%;
        left: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(0deg, transparent, #03e9f4);
        animation: animate4 1s linear infinite;
        animation-delay: 0.75s;
    }
    ${props => (props.f ? `filter: hue-rotate(${props.f})` : '')}
`;

function App({num,filter}) {
    let buttonRef = useRef(null);

    function onClick(e) {
        if (buttonRef?.current) {
            let x = e.clientX - buttonRef.current.getBoundingClientRect().left;
            let y = e.clientY - buttonRef.current.getBoundingClientRect().top;

            let ripples = document.createElement("span");
            ripples.style.left = x + 'px';
            ripples.style.top = y + 'px';

            buttonRef.current.append(ripples);

            setTimeout(() => {
                ripples.remove()
            },1000)
        }
    }

    return (
        <Button f={filter} ref={buttonRef} num={num} onClick={onClick}>
            <span className='neon-button_nth-child1'/>
            <span className='neon-button_nth-child2'/>
            <span className='neon-button_nth-child3'/>
            <span className='neon-button_nth-child4'/>
            Neon
        </Button>
    );
}

export default App;
