import React, {useEffect, useRef} from 'react';
import styled from "styled-components";

let Button = styled.button`
    position: relative;
    padding: 20px 50px;
    margin: 20vh 0;
    color: #2196f3;
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 2px;
    border: 0;
    overflow: hidden;
    transition: 0.2s;
    background: rgba(97,218,251,0);
    cursor: pointer;
    :hover {
        background: #2196f3;
        color: #255784;
        box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3,0 0 80px #2196f3;
        transition-delay: 1s;
        .neon-button_nth-child1 {
            left: 100%;
            transition: 0.5s;
        }
        .neon-button_nth-child2 {
            top: 100%;
            transition: 0.5s;
            transition-delay: 0.25s;
        }
        .neon-button_nth-child3 {
            left: -100%;
            transition: 0.5s;
            transition-delay: 0.5s;
        }
        .neon-button_nth-child4 {
            top: -100%;
            transition: 0.5s;
            transition-delay: 0.75s;
        }
    }
    > span {
        position: absolute;
        display: block;
    }
    .neon-button_nth-child1 {
        top: 0;
        left: -100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(90deg, transparent, #2196f3);
    }
    .neon-button_nth-child2 {
        top: -100%;
        right: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(180deg, transparent, #2196f3);
    }
    .neon-button_nth-child3 {
        bottom: 0;
        left: 100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(-90deg, transparent, #2196f3);
    }
    .neon-button_nth-child4 {
        top: 100%;
        left: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(0deg, transparent, #2196f3);
    }
`;

function App({num}) {
    let buttonRef = useRef(null);

    function onClick(e) {
        if (buttonRef?.current) {
            let x = e.clientX - buttonRef.current.getBoundingClientRect().left;
            let y = e.clientY - buttonRef.current.getBoundingClientRect().top;

            let ripples = document.createElement("span");
            ripples.style.left = x + 'px';
            ripples.style.top = y + 'px';

            buttonRef.current.append(ripples);

            setTimeout(() => {
                ripples.remove()
            },1000)
        }
    }

    return (
        <Button ref={buttonRef} num={num} onClick={onClick}>
            <span className='neon-button_nth-child1'/>
            <span className='neon-button_nth-child2'/>
            <span className='neon-button_nth-child3'/>
            <span className='neon-button_nth-child4'/>
            Neon
        </Button>
    );
}

export default App;
