import React, {useEffect,useRef} from 'react';
import styled from "styled-components";

let ScrollPath = styled.div`
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    height: 100%;
    background: rgda(255,255,255,0.05);
`;

let ProgressBar = styled.div`
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    background: linear-gradient(to top, #008aff, #00ffe7);
    animation: animate 5s linear infinite;
    :before {
      content: '';
      position: absolute;
      top:0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to top, #008aff, #00ffe7);
      filter: blur(10px);
    }
    :after {
      content: '';
      position: absolute;
      top:0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to top, #008aff, #00ffe7);
      filter: blur(30px);
    }
`;

function App() {
    let ProgressBarRef = useRef(null);
    useEffect(() => {
        if(ProgressBarRef?.current){
            console.log('here');
            window.onscroll = function () {
                let totalHeight = document.body.scrollHeight - window.innerHeight;
                let progressHeight = window.pageYOffset/totalHeight * 100;
                ProgressBarRef.current.style.height = progressHeight + '%';
            }
        }
    },[ProgressBarRef]);
    return (
        <ScrollPath>
            <ProgressBar ref={ProgressBarRef}/>
        </ScrollPath>
    );
}

export default App;
