import React, {useEffect, useRef} from 'react';
import styled from "styled-components";

let colorLeftBack = '#12d245';
let colorRightBack = '#f7f7f7';

let Container = styled.div`
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;
`;

let ParallaxLeft = styled.div`
    width: 100vw;
    height: 100vh;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 0;
    top: 0;
    background: ${colorLeftBack};
    z-index: 1;
    -webkit-clip-path: polygon(0 0, 55% 0, 45% 100%, 0% 100%);
    clip-path: polygon(0 0, 55% 0, 45% 100%, 0% 100%);
    >h2 {
        color: ${colorRightBack};
        position: relative;
        font-size: 140px;
    }
`;

let ParallaxRight = styled.div`
    width: 100vw;
    height: 100vh;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 0;
    top: 0;
    background: ${colorRightBack};
    z-index: 0;
    >h2 {
        color: ${colorLeftBack};
        position: relative;
        font-size: 140px;
    }
`;

function App() {
    let ParallaxLeftRef = useRef(null);
    let ParallaxRightRef = useRef(null);
    let ContainerRef = useRef(null);

    useEffect(() =>{
        if(ParallaxLeftRef?.current && ParallaxRightRef?.current && ContainerRef?.current){
            document.body.addEventListener('mousemove',(e)=>{
                let width = ContainerRef.current.getBoundingClientRect().width;
                let height = ContainerRef.current.getBoundingClientRect().height;

                let x = ( width / 2 - e.clientX ) / 10;
                let y = ( height / 2 - e.clientY ) / 10;

                ParallaxLeftRef.current.style.transform = `translate(${x}px,${y}px)`;
                ParallaxRightRef.current.style.transform = `translate(${x}px,${y}px)`;
            })
        }
    },[ParallaxLeftRef]);

    return (
        <Container ref={ContainerRef}>
            <ParallaxLeft >
                <h2 ref={ParallaxLeftRef}>
                    Parallax
                </h2>
            </ParallaxLeft>
            <ParallaxRight >
                <h2 ref={ParallaxRightRef}>
                    Parallax
                </h2>
            </ParallaxRight>
        </Container>
    );
}

export default App;
